import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController } from 'ionic-angular';
import { LaunchNavigator, LaunchNavigatorOptions } from '@ionic-native/launch-navigator';


@Component({
  selector: 'page-chat',
  templateUrl: 'chat.html',
})
export class ChatPage {
  segmentVar: boolean = true
  mockApi: boolean = false
  msgs:  any = []
  msgInput: string
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private launchNavigator: LaunchNavigator,
    public loadingCtrl: LoadingController
  ) {
  }
  ionViewDidLoad() {
    setTimeout(this.timeMockDataUser, 1000);
    let loading = this.loadingCtrl.create({
      spinner: 'crescent',
      content: 'Carregando pessoas'
    });

    loading.present();
    console.log('Opened')

    setTimeout(() => {
      this.mockApi = true
      console.log('closed')
      loading.dismiss();
    }, 1500);
  }
  chatMapa(d) {
    console.log('D ', d)
    this.segmentVar = d
  }
  openUber() {
    let options: LaunchNavigatorOptions = {
      start: 'Av. Dr. Cardoso de Melo, 90 - Vila Olimpia, So Paulo - SP, 04548-000',
    };

    this.launchNavigator.navigate(' R. Anglica, 100 - Jd. das Flores, Osasco - SP, 06132-380', options)
      .then(
        success => console.log('Launched navigator'),
        error => console.log('Error launching navigator', error)
      );
  }
  sendMsg(msgRec){
    this.msgs.push({ msg: msgRec})
    this.msgInput = ""
  }
  timeMockDataUser() {

    this.mockApi = false
  }
}



