import { ErrorHandler, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { AutorizaOPage } from '../pages/autoriza-o/autoriza-o';
import { CartTabDefaultPagePage } from '../pages/cart-tab-default-page/cart-tab-default-page';
import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';
import { QuerVirComigoPage } from '../pages/quer-vir-comigo/quer-vir-comigo';

import { CameraTabDefaultPagePage } from './../pages/camera-tab-default-page/camera-tab-default-page';
import { ChatPage } from './../pages/chat/chat';
import { LoginPage } from './../pages/login/login';
import { QuemVaiComigoPage } from './../pages/quem-vai-comigo/quem-vai-comigo';
import { MyApp } from './app.component';

import { LaunchNavigator } from '@ionic-native/launch-navigator';
import { RequestsProvider } from '../providers/requests/requests';
import { HttpClientModule } from '@angular/common/http';
import { Geolocation } from '@ionic-native/geolocation';


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ListPage,
    LoginPage,
    AutorizaOPage,
    CameraTabDefaultPagePage,
    CartTabDefaultPagePage,
    QuerVirComigoPage,

    QuemVaiComigoPage,
    ChatPage,
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    
  ]
}),
    HttpClientModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ListPage,
    LoginPage,
    AutorizaOPage,
    CameraTabDefaultPagePage,
    CartTabDefaultPagePage,
    QuerVirComigoPage,
    QuemVaiComigoPage,
    ChatPage,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    LaunchNavigator,
    RequestsProvider,
    Geolocation
    // HttpClient
  ]
})
export class AppModule {}



