import { ChatPage } from './../chat/chat';
import { Component } from '@angular/core';
import { NavController, LoadingController } from 'ionic-angular';

@Component({
  selector: 'page-quem-vai-comigo',
  templateUrl: 'quem-vai-comigo.html'
})
export class QuemVaiComigoPage {
  mockApi: boolean = false

  constructor(
    public navCtrl: NavController,
    public loadingCtrl: LoadingController
  ) {
  }
  ChatPagePush(){
    this.navCtrl.push(ChatPage)
  }
  ionViewDidLoad() {
    let loading = this.loadingCtrl.create({
      spinner: 'crescent',
      content: 'Carregando pessoas'
    });
    loading.present();
    console.log('Opened')
    setTimeout(() => {
      this.mockApi = true
      console.log('closed')
      loading.dismiss();
    }, 1500);
  }
  timeMockDataUser() {
    console.log('Mock')
    this.mockApi = false
  }

}



