import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AutorizaOPage } from './../autoriza-o/autoriza-o';
import { QuerVirComigoPage } from './../quer-vir-comigo/quer-vir-comigo';

@Component({
  selector: 'page-login',
  templateUrl: 'login.html'
})
export class LoginPage {

  constructor(public navCtrl: NavController) {
    // this.navCtrl.setRoot(QuerVirComigoPage)
  }
  querVirComigoPagePush(){
    this.navCtrl.setRoot(QuerVirComigoPage)
  }
  autorizaOPagePush(){
    this.navCtrl.push(AutorizaOPage)
  }

}



