import { QuemVaiComigoPage } from './../quem-vai-comigo/quem-vai-comigo';
import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { RequestsProvider } from '../../providers/requests/requests';
import { Geolocation } from '@ionic-native/geolocation';

@Component({
  selector: 'page-quer-vir-comigo',
  templateUrl: 'quer-vir-comigo.html'
})
export class QuerVirComigoPage {
  origin: string
  destiny: string
  resp: any
  constructor(
    public navCtrl: NavController,
    private req: RequestsProvider,
    private geolocation: Geolocation
  ) {
  }
  ionViewDidEnter(){
    this.geolocation.getCurrentPosition().then((resp) => {
      // resp.coords.latitude

      // resp.coords.longitude
      this.req.transformGeolocation({
        address: `${resp.coords.latitude},${resp.coords.longitude}`
      })
        .subscribe(resp =>{
          this.resp = resp
          this.origin = this.resp.address
          console.log(this.resp)
        })
     }).catch((error) => {
       console.log('Error getting location', error)
     })

  }
  QuemVaiComigoPagePush(){
    this.navCtrl.push(QuemVaiComigoPage)
  }

}



